import React from 'react';
import './BgAnimation.css';
import gear from './gear.png';

const BgAnimation = () => {
    return(
        <div id="animation">
            <img id="gear1" src={ gear }/>
            <img id="gear2" src={ gear }/>
            <img id="gear3" src={ gear }/>
        </div>
    );
}

export default BgAnimation;