import React from 'react';
import yoshibrain from './yoshibrain.png';
import './Portfolio.css';

const Portfolio = () => {
    return(
        <div>
            <div className="portfolioTitle--">
                <div id="logoName" className="portfolioTitle">
                    <div className="portfolioTitle-">PORTFOLIO</div>                
                </div>
            </div>
            <div class="grid-container images">
		  		<div class="griddiv">
                    <div className="cardImageContainer">
                        <img className="cardImage" src={ yoshibrain }></img>
                    </div>
                    <div style={{ fontWeight: 'bold' , fontSize: '1.2em' , padding: '5px' }}>Yoshibrain/Smartbrain</div>
                    <div style={{ padding: '5px' }} >Smartbrain/Yoshibrain is a webApp that uses the Clarifai API to detect and mark faces from Images via Images URL.</div>
                    <div>
                        <a style={{ padding: '2px' , paddingRight: '8px' }} href="https://github.com/AfootDesert41/smartbrain" target="_blank">Github</a>
                        <a style={{ padding: '2px' }} href="https://yoshibrain.herokuapp.com/" target="_blank">Webpage</a>
                    </div>
                </div>
		  	</div>
        </div>
    );
}

export default Portfolio;