import React from 'react';
import { Navbar , Nav , Icon } from 'rsuite';
import 'rsuite/dist/styles/rsuite-default.css';
import './Navigation.css'
import logo from './logo.png';

const Navigation = ({ onRouteChangePortfolio , onRouteChangeAbout , cvDownload }) => {
    return(
        <Navbar appearance="none" className="navBar">
            <Navbar.Header id="logo" onclick={ onRouteChangePortfolio }>
            <a href="">
                <img id="logoImg" src={ logo } ></img>
            </a>
                <div className="name" style={{ cursor: 'pointer' }}>
                    <div id="logoName">GUSTAVO RIOS</div>
                    <div id="logoTitle">{"{ "}fullStack webDeveloper{" }"}</div>
                </div>
            </Navbar.Header>
            <Navbar.Body>
                <Nav pullRight>
                    <Nav.Item icon={<Icon icon="folder" />} onClick={onRouteChangePortfolio} >Portfolio</Nav.Item>
                    <Nav.Item icon={<Icon icon="info" />} onClick={onRouteChangeAbout} >About Me</Nav.Item>
                    <Nav.Item icon={<Icon icon="avatar" />} onClick={cvDownload} >CV / Resume</Nav.Item>
                </Nav>
            </Navbar.Body>
        </Navbar>
    );
}

export default Navigation;