import React from 'react';
import pic from './pic.jpg';
import './About.css';

const About = () => {
    return(
        <div>
            <div className="portfolioTitle--">
                <div id="logoName" className="portfolioTitle">
                    <div className="portfolioTitle-">ABOUT ME</div>                
                </div>
            </div>
            <div id="container">
                <div style={{display: 'flex'}}>
                    <div style={{display: 'flex' , justifyContent: 'center'}}>
                        <img src={pic} id="pic" width="200" height="200" style={{ borderRadius: '10px' , display: 'flex' , alignItems: 'center' }} ></img>
                    </div>
                    <div>
                        <p style={{ padding: '20px' , fontFamily: 'codec-subtitle' , fontSize: '1.2em' }}>Hello, my name is Gustavo Rios. I'm a fullStack webDeveloper and Informatics Engineering student at Universidad Católica Andrés Bello (UCAB). I'm starting to get into the Web Development market and this website's purpose is to show my capabilities as a Web Developer. You can watch some of my work by clicking the 'Portfolio' tab on the top right. Or if you would like to take a look at my resume you can click the 'CV / Resume' tab on the top right.</p>     
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;