import React, { Component } from 'react';
import './App.css';
import 'rsuite/dist/styles/rsuite-default.css';
import Navigation from '../Components/Navigation/Navigation';
import BgAnimation from '../Components/BgAnimation/BgAnimation';
import Portfolio from '../Components/Portfolio/Portfolio';
import About from '../Components/About/About';

const initialState = {
  route: 'Portfolio'
}

class App extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

  onRouteChangePortfolio = () => {
    this.setState({route: 'Portfolio'});
  }

  onRouteChangeAbout = () => {
    this.setState({route: 'About'});
  }

  cvDownload = () => {
    window.open('https://drive.google.com/file/d/1qsfIxK4UjIFK45bKE1endNRZaTBJ80tD/view', '_blank');
  }

  render() {
    return(
      <div className="content">
        { this.state.route === 'Portfolio'
        ? <div> 
            <Navigation onRouteChangePortfolio={this.onRouteChangePortfolio} onRouteChangeAbout={this.onRouteChangeAbout} cvDownload={this.cvDownload} />
            <BgAnimation />
            <Portfolio />
          </div>
        : <div> 
            <Navigation onRouteChangePortfolio={this.onRouteChangePortfolio} onRouteChangeAbout={this.onRouteChangeAbout} cvDownload={this.cvDownload} />
            <BgAnimation />
            <About />
          </div>
        }
      </div>
    );
  }
}

export default App;
